___CSS_LOADER_EXPORT___.push([module.id, `/* ===== layout ===== */

.cards {
  display: flex;
  justify-content: center;
  padding: 5rem;
}

.card-wrapper {
  position: relative;
  cursor: pointer;
}

/* ===== card ===== */

.card {
  width: 320px;
  height: 200px;
  padding: 2rem;
  background: #222;
  color: #fff;
  border-radius: 10px;
  z-index: 2;
  position: relative;

  transition: transform 0.4s ease;
}

/* ===== detail panel ===== */

.card-detail {
  position: absolute;
  top: 0;
  left: 0;

  width: 480px;
  height: 100%;
  padding: 2rem 2rem 2rem 360px;

  background: #111;
  color: #fff;
  border-radius: 10px;

  opacity: 0;
  transform: translateX(40px);
  pointer-events: none;

  transition:
    opacity 0.4s ease,
    transform 0.4s ease;
}

/* ===== active ===== */

.card-wrapper.active .card {
  transform: scale(1.15);
}

.card-wrapper.active .card-detail {
  opacity: 1;
  transform: translateX(0);
}


/* ===== cards ===== */
.cd-layout {
  display: flex;
  gap: 5rem;
  margin-top: 3rem;
}

/* wrapper */
.card-wrapper {
  position: relative;
  cursor: pointer;
}

/* card */
.card {
  width: 300px;
  height: 180px;
  padding: 2rem;
  background: #333;
  border-radius: 10px;
  transition: transform .4s;
  z-index: 2;
  position: relative;
}

/* detail */
.card-detail {
  position: absolute;
  inset: 0;
  padding: 2rem 2rem 2rem 340px;
  background: #111;
  border-radius: 10px;

  opacity: 0;
  transform: translateX(40px);
  pointer-events: none;

  transition: .4s;
}

/* active */
.card-wrapper.active .card {
  transform: scale(1.15);
}

.card-wrapper.active .card-detail {
  opacity: 1;
  transform: translateX(0);
}
